body {
  margin: 0;
  background-color: #212F3D;
  font-family: monospace;
}

.meme {
  position: relative;
  width: 100%;
  margin-top: 1.2em;
  text-align: center;
}

.meme>img {
  width: 95%;
  max-width: 40em;
  max-height: 40em;
  margin-bottom: 1.5em;
  background: cover;
  background-repeat: no-repeat;
}

.meme>h2 {
  position: absolute;
  max-width: 36em;
  text-align: center;
  left: 49%;
  transform: translateX(-50%);
  margin: 0.3em 0;
  padding: 0 0px;
  text-transform: uppercase;
  color: white;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000, -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000, 2px 2px 5px #000;
}

.meme>.bottom {
  bottom: 0.9em;
}

.meme>.top {
  top: 0.1em;
}

.memeForm {
  width: 90%;
  margin: 1.4em 0em 1em 1.4em;
  display: inline-block;
  justify-content: space-between;
}

.memeForm>input {
  border: 1.5px solid;
  border-radius: 5px;
  margin: 0.4em;
  display: block;
  width: 40%;
  min-width: 20em;
  height: 3em;
}

.memeForm>button {
  height: 2.1em;
  width: 14em;
  margin-top: 1em;
  margin-left: 0.1em;
  border: 3px rgb(221, 210, 210) solid;
  font-size: 20px;
  letter-spacing: 1.5px;
  color: white;
  background: #2E86C1;
}

.memeForm>button:hover {
  background: #3498DB;
}

.memeForm>input::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  font-size: 1.3em;
  text-align: left;
  padding-left: 5px;
}

.memeForm>input::-moz-placeholder {
  /* Firefox 19+ */
  font-size: 25px;
  text-align: left;
}

.memeForm>input:-ms-input-placeholder {
  /* IE 10+ */
  font-size: 25px;
  text-align: left;
}

.memeForm>input:-moz-placeholder {
  /* Firefox 18- */
  font-size: 25px;
  text-align: left;
}

label {
  color: white;
  margin-left: 9px;
  font-size: 23px;
}

h1 {
  color: white;
  margin-left: 9px;
  font-size: 40px;
}